.selectionSlide {
  background: #ECE9E6;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #FFFFFF, #ECE9E6);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, right top, left top, from(#FFFFFF), to(#ECE9E6));
  background: -webkit-linear-gradient(right, #FFFFFF, #ECE9E6);
  background: -o-linear-gradient(right, #FFFFFF, #ECE9E6);
  background: linear-gradient(to left, #FFFFFF, #ECE9E6); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.stepper_bar {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:row;
      flex-direction:row;
}
.selection {
  width: 100%;
  grid-template-columns: 7fr 3fr;
  grid-template-rows: 1fr;
  display: grid;
}
.usamap {
  margin: auto;
}
path {
  pointer-events: all;
}
path:hover {
  opacity: 0.50;
  cursor: pointer;
}
.representativeTable {
  margin: auto;
  padding: 25px;
  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: repeat(2, 1fr);
  display: grid;
}
.representativeTile {
  width: inherit;
  background: white;
  margin: 50px 25px 50px 15px;
}
.representativeTile:hover {
  opacity: 0.50;
  cursor: pointer;
}


.pieSlide {
  background: #ECE9E6;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #FFFFFF, #ECE9E6);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, right top, left top, from(#FFFFFF), to(#ECE9E6));
  background: -webkit-linear-gradient(right, #FFFFFF, #ECE9E6);
  background: -o-linear-gradient(right, #FFFFFF, #ECE9E6);
  background: linear-gradient(to left, #FFFFFF, #ECE9E6); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.pieGraphs {
  display: grid;
  grid-template-rows: 1fr 1fr;
  width: 100%;
}
.piePage {
  width: 100%;
  grid-template-columns: 1fr 1fr;
  display: grid;
  margin-top: auto;
}
.piePageLeft {
  width: 100%;
  height: 100%;
  padding: auto;
  white-space: nowrap;
}
.piePageRight {
  padding: auto;
}
.pieSearchBlock {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin: auto;
  padding: auto;
}
.tweetTable {
  height: 500px;
  width: 100%;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  display: grid;
}
.tweetTile {
  margin: 0 auto;
}

.lineSlide {
  background: #ECE9E6;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #FFFFFF, #ECE9E6);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, right top, left top, from(#FFFFFF), to(#ECE9E6));
  background: -webkit-linear-gradient(right, #FFFFFF, #ECE9E6);
  background: -o-linear-gradient(right, #FFFFFF, #ECE9E6);
  background: linear-gradient(to left, #FFFFFF, #ECE9E6); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.scatterSlide {
  background: #ECE9E6;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #FFFFFF, #ECE9E6);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, right top, left top, from(#FFFFFF), to(#ECE9E6));
  background: -webkit-linear-gradient(right, #FFFFFF, #ECE9E6);
  background: -o-linear-gradient(right, #FFFFFF, #ECE9E6);
  background: linear-gradient(to left, #FFFFFF, #ECE9E6); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.barSlide {
  background: #ECE9E6;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #FFFFFF, #ECE9E6);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, right top, left top, from(#FFFFFF), to(#ECE9E6));
  background: -webkit-linear-gradient(right, #FFFFFF, #ECE9E6);
  background: -o-linear-gradient(right, #FFFFFF, #ECE9E6);
  background: linear-gradient(to left, #FFFFFF, #ECE9E6); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.barPage {
  padding: 25px;
}



